<template>
  <div
    v-show="isShow"
    @click.self="cancel"
    class="modal"
  >
    <div class="modal__inner">
      <h3></h3>

      <div class="modal__body">{{ message }}</div>

      <button class="btn btn-secondary" @click="agree">{{ title }}</button>
      <button class="btn btn-link" @click="cancel"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Confirm',

  data() {
    return {
      isShow: false,
      resolve: null,
      reject: null,
      message: null,
      title: null,
    }
  },

  methods: {
    /**
    *     
    *   @param {string} title,   
    *   @param {string} message,  
     */
    open(title, message) {
      this.isShow = true
      this.title = title
      this.message = message
      return new Promise((resolve, reject) => {
        this.resolve = resolve
        this.reject = reject
      })
    },
    /**
    *    
     */
    agree() {
      this.resolve(true)
      this.isShow = false
    },
    /**
    *    
     */
    cancel() {
      this.resolve(false)
      this.isShow = false
    }
  }
}
</script>


